import React, { useState, useEffect, useRef } from "react";
import jsonData from "../api.json";
import "./SearchBar.css";
import { FiRotateCcw, FiSearch } from "react-icons/fi";
import NavDrop from "./NavDrop";

function SearchBar() {
  const [data, setData] = useState([]);
  const [searchTerm, setSearchTerm] = useState("");
  const [filteredData, setFilteredData] = useState([]);
  const [searched, setSearched] = useState(false);
  const [suggestions, setSuggestions] = useState([]);
  const suggestionRef = useRef();

  useEffect(() => {
    setData(jsonData); //  .
  }, []);

  //  useEffect
  useEffect(() => {
    if (searchTerm === "") {
      setSuggestions([]); //        .
    } else {
      const filtered = data.filter((item) =>
        item.title.toLowerCase().includes(searchTerm.toLowerCase())
      ); //      .
      setSuggestions(filtered); //     .
    }
  }, [searchTerm, data]);

  const handleSearchChange = (event) => {
    setSearchTerm(event.target.value); //   .
  };

  const handleFormSubmit = (event) => {
    event.preventDefault();
    if (searchTerm === "") {
      setFilteredData([]);
    } else {
      const filtered = data.filter((item) =>
        item.title.toLowerCase().includes(searchTerm.toLowerCase())
      );
      setFilteredData(filtered);
    }
    setSearched(true);
    setSuggestions([]);
  }; //     .

  const handleReset = () => {
    setSearchTerm("");
    setFilteredData([]);
    setSearched(false);
  };

  const handleSuggestionClick = (suggestions) => {
    setSearchTerm(suggestions.title);
    setFilteredData([suggestions]);
    setSearched(true);
    setSuggestions([]);
  }; //        .

  const handleOutsideClick = (event) => {
    if (
      suggestionRef.current &&
      !suggestionRef.current.contains(event.target)
    ) {
      setSuggestions([]); //         .
    }
  };

  useEffect(() => {
    document.addEventListener("click", handleOutsideClick);
    return () => {
      document.removeEventListener("click", handleOutsideClick);
    };
  }, []);

  useEffect(() => {
    if (filteredData.length > 0) {
      setSuggestions([]); //       .
    }
  }, [filteredData]);

  const handleKeyPress = (event) => {
    if (event.key === "Enter") {
      event.preventDefault();
      handleFormSubmit(event);
    }
  };

  return (
    <div>
      <NavDrop />
      <div className="SearchBar-container">
        <div className="Searchbar">
          <form className="Searchbar-form" onSubmit={handleFormSubmit}>
            <div className="Search-container">
              <div className="Search-input-container">
                <input
                  type="text"
                  value={searchTerm}
                  onChange={handleSearchChange}
                  onKeyPress={handleKeyPress}
                  placeholder=""
                  inputMode="text"
                />
                {searchTerm ? (
                  <button className="Search-ResetButton" onClick={handleReset}>
                    <i aria-hidden="true">
                      <FiRotateCcw />
                    </i>
                  </button>
                ) : (
                  <span style={{ width: "28px" }}></span> //handleReset     
                )}
                <button className="Search-SubmitButton" type="submit">
                  <FiSearch />
                </button>
              </div>
              {suggestions.length > 0 && (
                <ul className="Suggestion_title" ref={suggestionRef}>
                  {suggestions.map((suggestion) => (
                    <li
                      key={suggestion.id}
                      onClick={() => handleSuggestionClick(suggestion)}
                    >
                      {suggestion.title}
                    </li>
                  ))}
                </ul> // 
              )}
            </div>
          </form>

          {searched && filteredData.length === 0 && suggestions.length === 0 ? (
            <div>  .</div>
          ) : filteredData.length > 0 ? (
            <div className="Searchcard-container">
              {filteredData.map((item) => (
                <div className="Searchcard" key={item.id}>
                  <h4>{item.title}</h4>
                  <h5>{item.year}</h5>
                  <p>{item.content}</p>
                </div>
              ))}
            </div>
          ) : searched ? (
            <div>  .</div>
          ) : null}
        </div>
      </div>
    </div>

    //     .
  );
}

export default SearchBar;
