import PlusInput from "./PlusOption/PlusInput";
import PlusLabel from "./PlusOption/PlusLabel";
import PlusInsertButton from "./PlusOption/PulsInsertButton";
import PlusContainer from "./PlusOption/PlusContainer";
import { useRef } from "react";
import { useEffect } from "react";
//import axios from "axios";

function PlusPopup({ onClose }) {
  const popupRef = useRef(null);

  useEffect(() => {
    const handleOutsideClick = (e) => {
      if (popupRef.current && !popupRef.current.contains(e.target)) {
        onClose();
      }
    };

    document.addEventListener("mousedown", handleOutsideClick);
    return () => {
      document.removeEventListener("mousedown", handleOutsideClick);
    };
  }, [onClose]);

  const handleKeyDown = (e) => {
    if (e.key === "Enter") {
      e.preventDefault();
    }
  };

  const handleConfirmClick = () => {
    // ""         .
    const titleInput = document.getElementById("title");
    const sicneInput = document.getElementById("since");
    const yearInput = document.getElementById("year");
    const contentInput = document.getElementById("content");

    // eslint-disable-next-line
    const newItem = {
      title: titleInput.value,
      since: sicneInput.value,
      year: yearInput.value,
      content: contentInput.value,
    };

    /* Axios  API   .
    axios
      .post("https://example-api.com/items", newItem)
      .then((response) => {
        console.log("  !", response.data);
        onClose(); // API    .
      })
      .catch((error) => {
        console.error("  :", error);
      });
      */

    onClose();
  };

  return (
    <PlusContainer onKeyDown={handleKeyDown} tabIndex="0">
      <form ref={popupRef}>
        <h2></h2>
        <PlusLabel htmlFor="title"></PlusLabel>
        <PlusInput text="" id="title" name="title" placeholder="" />
        <PlusLabel htmlFor="since"></PlusLabel>
        <PlusInput text="" id="since" name="since" placeholder="" />
        <PlusLabel htmlFor="year"></PlusLabel>
        <PlusInput text="" id="year" name="year" placeholder="" />
        <PlusLabel htmlFor="content"></PlusLabel>
        <PlusInput text="" id="content" name="content" placeholder="" />
        <PlusInsertButton onClick={handleConfirmClick}></PlusInsertButton>
      </form>
    </PlusContainer>
  );
}

export default PlusPopup;
